<template>
    <a href="#" class="b-download-button">
        <span :class="{ 'b-download-button__icon': true, 'b-download-button__icon_type_ios': iconType === 'ios', 'b-download-button__icon_type_android': iconType === 'android' }"></span>
    </a>
</template>

<script>
export default {
  name: 'DownloadComponent',
  props: {
    link: {
      type: String,
      required: true,
      default: '#'
    },
    iconType: {
      type: String,
      required: true
    }
  }
}
</script>

<style lang="scss">
    .b-download-button {
        width: 196px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;

        text-decoration: none;
        outline: none;

        border-radius: 25px;
        background-color: #fff;

        transition: .25s ease;

        &__icon {
            height: 33px;
            display: inline-block;

            background-position: center;
            background-repeat: no-repeat;

            &_type_ios {
                width: 133px;

                background-image: url('../assets/Download-IOS.svg');
            }
            &_type_android {
                width: 137px;

                position: relative;
                top: 2px;

                background-image: url('../assets/Download-Android.svg');
            }
        }

        &:hover,
        &:focus {
            transform: scale(0.95);
        }
    }
</style>



