<template>
    <div class="b-heading">
        <h3 class="b-heading__title">{{ title }}</h3>
        <p v-show="content !== null" class="b-heading__content">{{ content}}</p>
    </div>
</template>

<script>
export default {
  name: 'HeadingComponent',
  props: {
    title: {
      type: String,
      default: null,
      required: true
    },
    content: {
      type: String,
      default: null
    }
  }
}
</script>

<style lang="scss">
    .b-heading {
        text-align: center;

        &__title {
            margin: 0;
            color: #262626;
            font-weight: bold;
            font-size: 36px;
            line-height: 43px;
        }

        &__content {
            max-width: 534px;
            margin: 14px auto 0 auto;

            color: rgba(38, 38, 38, 0.5);
            font-weight: 500;
            line-height: 26px;
            font-size: 16px;
        }
    }

    @media (max-width: 768px) {
        .b-heading {
            &__title {
                font-size: 24px;
                line-height: 29px;
            }

            &__content {
                margin: 18px 0 0 0;
                font-size: 14px;
                line-height: 24px;
            }
        }
    }
</style>



