<template>
    <div class="b-container"><slot></slot></div>
</template>

<script>
export default {
  name: 'ContainerComponent'
}
</script>

<style lang="scss">
    .b-container {
        max-width: 1110px;
        margin: 0 auto;
        padding: 0 15px;
    }

    @media (max-width: 1200px) {
        .b-container {
            max-width: 960px;
        }
    }

    @media (max-width: 992px) {
        .b-container {
            max-width: 720px;
        }
    }

    @media (max-width: 768px) {
        .b-container {
            max-width: 540px;
        }
    }

    @media (max-width: 576px) {
        .b-container {
            max-width: 100%;
        }
    }
</style>



