<template>
    <a :href="link" :class="theme"><slot></slot></a>
</template>

<script>
export default {
  name: 'ButtonComponent',
  props: {
    link: {
      type: String,
      required: false,
      default: '#'
    },
    theme: {
      type: Array,
      required: true
    }
  }
}
</script>

<style lang="scss">
    .b-button {
        display: inline-block;
        line-height: 40px;
        padding: 0 23px;

        color: #fff;
        font-size: 14px;
        font-weight: bold;
        text-transform: uppercase;
        text-decoration: none;
        letter-spacing: 1.75px;
        outline: none;

        transition: .15s ease;

        &_theme_primary {
            border-radius: 20px;
            background-color: #EB5353;

            &:hover {
                background-color: #ff5a5a;
            }

            &:focus {
                background-color: #d54c4c;
            }
        }
    }
</style>



