<template>
    <section class="b-news">
        <container-component>
            <news-item-component news-type="item" category="Announcements" title-href="#" title="Join the #CryptoLife hackathon" text="Book you ticket for Devcon a weekend early and join us from the 26th - 28th October in Prague to #build the distributed future!" link-href="#" link-name="Learn more and apply" />
            <news-item-component news-type="item" category="Reyna principles" title-href="#" title="A Principled Approach" text="REYNA is a community of people all over the world connected by a set of values and principles." link-href="#" link-name="See the full set of principles" />
            <news-item-component news-type="list" category="Latest news" link-href="#" link-name="Read the blog" :articles="articles" />
        </container-component>
    </section>
</template>

<script>
import ContainerComponent from '@/components/ContainerComponent'
import NewsItemComponent from '@/components/NewsItemComponent'

export default {
  name: 'NewsComponent',
  components: {
    ContainerComponent,
    NewsItemComponent
  },
  data () {
    return {
      articles: [
        {
          id: 0,
          date: '7 nov at 2;42',
          title: 'Today we like to present new UI',
          href: '#'
        },
        {
          id: 1,
          date: '31 oct at 18:23',
          title: 'ReynaChat (Alpha)',
          href: '#'
        }
      ]
    }
  }
}
</script>

<style lang="scss">
    .b-news {
        .b-container {
            display: flex;

            .b-news-item {
                &:not(:last-child) {
                    border-right: 1px solid #FFE3E3;
                }

                &:first-child {
                    border-top-left-radius: 10px;
                    border-bottom-left-radius: 10px;
                }

                &:last-child {
                    border-top-right-radius: 10px;
                    border-bottom-right-radius: 10px;
                }
            }
        }
    }

    @media (max-width: 768px) {
        .b-news {
            margin: 85px 0 0 0;

            .b-container {
                max-width: 100%;
                padding: 0;

                flex-direction: column;

                .b-news-item {
                    box-sizing: border-box;

                    &:first-child,
                    &:last-child,
                    &:not(:last-child){
                        border: none;
                        border-radius: 0;
                    }

                    &:not(:last-child) {
                        border-bottom: 1px solid #FFE3E3;
                    }
                }
            }
        }
    }
</style>



