<template>
    <div v-if="newsType === 'item'" class="b-news-item">
        <div class="b-news-item__category">{{ category }}</div>
        <div class="b-news-item__content">
            <a :href="titleHref" class="b-news-item__title">{{ title }}</a>
            <p class="b-news-item__text">{{ text }}</p>
        </div>
        <learn-more-component :href="linkHref">{{ linkName }}</learn-more-component>
    </div>
    <div v-else-if="newsType === 'list'" class="b-news-item">
        <div class="b-news-item__category">{{ category }}</div>
        <div class="b-news-item__content">
            <div v-for="article in articles" v-bind:key="article.id" class="b-news-item__article">
                <div class="b-news-item__date">{{ article.date }}</div>
                <a :href="article.href" class="b-news-item__article-title">{{ article.title }}</a>
            </div>
        </div>
        <learn-more-component :href="linkHref">{{ linkName }}</learn-more-component>
    </div>
</template>

<script>
import LearnMoreComponent from '@/components/LearnMoreComponent'

export default {
  name: 'NewsItemComponent',
  components: {
    LearnMoreComponent
  },
  props: {
    newsType: {
      type: String,
      default: 'item'
    },
    category: {
      type: String,
      required: true
    },
    titleHref: {
      type: String,
      default: '#'
    },
    title: {
      type: String
    },
    text: {
      type: String
    },
    linkHref: {
      type: String,
      default: '#'
    },
    linkName: {
      type: String
    },
    articles: {
      type: Array
    }
  }
}
</script>

<style lang="scss">
    .b-news-item {
        width: 100%;
        padding: 31px 23px 27px 29px;
        background-color: #fff;

        &__content {
            height: 190px;
            overflow: hidden;
        }

        &__category {
            margin-bottom: 21px;

            color: rgba(38, 38, 38, 0.5);
            font-size: 14px;
            font-weight: 500;
            letter-spacing: 1.75px;
            text-transform: uppercase;
        }

        &__title {
            color: #262626;
            font-size: 18px;
            font-weight: bold;
            line-height: 21px;
            text-decoration: none;
            outline: none;

            transition: .15s ease;
            &:hover,
            &:focus {
                color: #EB5353;
            }
        }

        &__text {
            margin: 13px 0 0 0;

            color: rgba(38, 38, 38, 0.5);
            font-size: 16px;
            font-weight: 500;
            line-height: 26px;

            overflow: hidden;
        }

        &__article:not(:last-child) {
            margin-bottom: 25px;
        }

        &__date {
            color: rgba(38, 38, 38, 0.5);
            font-size: 12px;
            font-weight: 500;
            line-height: 26px;
            text-transform: uppercase;
            letter-spacing: 1px;
        }

        &__article-title {
            color: #262626;
            font-size: 18px;
            font-weight: bold;
            line-height: 21px;
            text-decoration: none;

            transition: .15s ease;
            outline: none;
            &:hover,
            &:focus {
                color: #EB5353;
            }
        }
    }

    @media (max-width: 992px) {
        .b-news-item {
            .b-learn-more {
                font-size: 14px;
            }
        }
    }

    @media (max-width: 768px) {
        .b-news-item {
            .b-learn-more {
                font-size: 16px;
            }
        }
    }
</style>



