<template>
    <section class="b-card">
        <container-component>
            <heading-component title="The Reyna Ecosystem" content="#ReynaProject is more than an app - it's a community of people creating the tools and infrastructure we need to #build a better web. Our collective goal is the mass adoption of Tron Foundation" />
            <div class="b-card__wrap">
                <card-item-component title-link="#" title="ReynaChat" content="Chat in safest application using own encryption protocol. Test the mobile alpha." button-link="#" button-text="Learn More" />
                <card-item-component title-link="#" title="ReynaWallet" content="Wallet works with any TRC20 tokens and supports the main blockchain in the Tron Foundation. " button-link="#" button-text="Learn More" />
                <card-item-component title-link="#" title="ReynaProtocol" content="Reyna protocols support mutual and optional authentication, identity hiding, forward secrecy, zero round-trip encryption, and other advanced features." button-link="#" button-text="Learn More" />
            </div>
        </container-component>
    </section>
</template>

<script>
import ContainerComponent from '@/components/ContainerComponent'
import HeadingComponent from '@/components/HeadingComponent'
import CardItemComponent from '@/components/CardItemComponent'

export default {
  name: 'CardComponent',
  components: {
    ContainerComponent,
    HeadingComponent,
    CardItemComponent
  }
}
</script>

<style lang="scss">
    .b-card {
        margin: 90px 0 0 0;
        &__wrap {
            margin: 39px 0 0 0;
            display: flex;
        }

        .b-card-item {
            &:not(:last-child) {
                margin-right: 30px;
            }
        }
    }

    @media (max-width: 768px) {
        .b-card {
            margin: 52px 0 0 0;

            &__wrap {
                flex-direction: column;
            }

            .b-card-item:not(:last-child) {
                margin: 0 0 15px 0;
            }
        }
    }
</style>



