<template>
    <section class="lead">
        <container-component>
            <h1 class="lead__heading">Access a Better Web, Anywhere</h1>
            <p class="lead__desk">Chat, browse and transact securely in an open source community committed to bringing the power of Tron Foundation and a more distributed internet to your pocket.</p>
            <div class="lead__download">
                <download-component link="#" icon-type="ios" />
                <download-component link="#" icon-type="android" />
            </div>
        </container-component>
    </section>
</template>

<script>
import ContainerComponent from '@/components/ContainerComponent'
import DownloadComponent from '@/components/DownloadComponent'

export default {
  name: 'LeadComponent',
  components: {
    ContainerComponent,
    DownloadComponent
  }
}
</script>

<style lang="scss">
    .lead {
        margin: 61px 0 0 0;
        text-align: center;

        &__heading {
            margin: 0;

            color: #262626;
            font-size: 46px;
            font-weight: bold;
        }

        &__desk {
            max-width: 537px;
            margin: 0 auto;

            color: rgba(0, 0, 0, 0.5);
            font-size: 16px;
            font-weight: 500;
            line-height: 26px;
            letter-spacing: 0;
        }

        &__download {
            margin: 40px 0 0 0;

            display: flex;
            align-items: center;
            justify-content: center;

            .b-download-button:not(:last-child) {
                margin-right: 30px;
            }
        }

        .b-container {
            position: relative;

            &:after {
                content: "";

                width: 994px;
                height: 380px;
                display: inline-block;

                position: relative;
                left: -30px;
                pointer-events: none;
                z-index: 1;
                margin: 77px 0 -88px 0;

                background-image: url('../assets/Blocks.svg');
            }
        }
    }

    @media (max-width: 1200px) {
        .lead {
            .b-container {
                &:after {
                    width: 100%;
                    left: 0;
                    -webkit-background-size: cover;
                    background-size: cover;
                }
            }
        }
    }

    @media (max-width: 992px) {
        .lead {
            .b-container {
                &:after {
                    height: 270px;
                    margin: 77px 0 -69px 0;
                }
            }
        }
    }

    @media (max-width: 768px) {
        .lead {
            margin: 29px 0 0 0;

            &__heading {
                font-size: 24px;
                line-height: 29px;
            }

            &__desk {
                margin: 19px auto 0 auto;

                font-size: 14px;
                line-height: 24px;
                max-width: 290px;
            }

            &__download {
                margin: 22px 0 0 0;
                flex-direction: column;

                .b-download-button:not(:last-child) {
                    margin: 0 0 30px 0;
                }
            }

            .b-container {
                &:after {
                    display: none;
                }
            }
        }
    }
</style>



