<template>
    <article class="b-card-item">
        <a :href="titleLink" class="b-card-item__title">{{ title }}</a>
        <p :class="objectClass">{{ content }}</p>
        <learn-more-component :href="buttonLink">{{ buttonText }}</learn-more-component>
    </article>
</template>

<script>
import LearnMoreComponent from '@/components/LearnMoreComponent'

export default {
  name: 'CardItemComponent',
  components: {
    LearnMoreComponent
  },
  props: {
    titleLink: {
      Type: String,
      required: true,
      default: '#'
    },
    title: {
      Type: String,
      required: true
    },
    content: {
      Type: String,
      required: true
    },
    buttonLink: {
      Type: String,
      required: true,
      default: '#'
    },
    buttonText: {
      Type: String,
      required: true
    }
  },
  computed: {
    objectClass: function () {
      return {
        'b-card-item__text': true,
        'b-card-item__text_style_fluid': this.content.length >= 120
      }
    }
  }
}
</script>

<style lang="scss">
    .b-card-item {
        width: 100%;
        background-color: #fff;

        padding: 23px 23px 18px 25px;
        border-radius: 10px;

        &__title {
            color: #262626;
            font-size: 20px;
            font-weight: bold;
            text-decoration: none;
            outline: none;

            transition: .15s ease;

            &:hover,
            &:focus {
                color: #EB5353;
            }
        }

        &__text {
            margin: 12px 0 0 0;
            max-width: 260px;
            height: 130px;
            overflow: hidden;

            color: rgba(38, 38, 38, 0.5);
            font-weight: 500;
            font-size: 16px;
            line-height: 26px;

            &_style_fluid {
                max-width: 100%;
            }
        }
    }

    @media (max-width: 768px) {
        .b-card-item {
            box-sizing: border-box;

            &__text {
                height: auto;
                min-height: 100px;
                margin: 12px 0 22px 0;
            }
        }
    }
</style>



