<template>
    <header :class="headerClass">
        <container-component>
            <a href="#" class="b-header__home">
                <img src="../assets/Logo.svg" class="b-header__logo" alt="Reyna">
                <span class="b-header__title">Reyna</span>
            </a>
            <ul :class="navClass">
                <li class="b-header__item">
                    <a href="#" class="b-header__link">App</a>
                </li>
                <li class="b-header__item">
                    <a href="#" class="b-header__link">Docs</a>
                </li>
                <li class="b-header__item">
                    <div class="b-header__link b-header__link_hasGroup_yes">
                        <span @click="underMenuChangeState(underMenuConstructor[0].id)" class="b-header__span">Projects</span>
                        <ul class="b-header__mobile">
                            <li class="b-header__mobile-item">
                                <a href="#" class="b-header__mobile-link">ReynaChat</a>
                            </li>
                            <li class="b-header__mobile-item">
                                <a href="#" class="b-header__mobile-link">ReynaWallet</a>
                            </li>
                            <li class="b-header__mobile-item">
                                <a href="#" class="b-header__mobile-link">ReynaProtocol</a>
                            </li>
                        </ul>
                        <under-menu-component :state="underMenuConstructor[0].status" :title="underMenuConstructor[0].title" :description="underMenuConstructor[1].description" :data="underMenuConstructor[0].data" @onClose="underMenuChangeState(underMenuConstructor[0].id)" />
                    </div>
                </li>
                <li class="b-header__item">
                    <div class="b-header__link b-header__link_hasGroup_yes">
                        <span @click="underMenuChangeState(underMenuConstructor[1].id)" class="b-header__span">Contribute</span>
                        <ul class="b-header__mobile">
                            <li class="b-header__mobile-item">
                                <a href="#" class="b-header__mobile-link">ReynaChat</a>
                            </li>
                            <li class="b-header__mobile-item">
                                <a href="#" class="b-header__mobile-link">ReynaWallet</a>
                            </li>
                            <li class="b-header__mobile-item">
                                <a href="#" class="b-header__mobile-link">ReynaProtocol</a>
                            </li>
                        </ul>
                        <under-menu-component :state="underMenuConstructor[1].status" :title="underMenuConstructor[1].title" :description="underMenuConstructor[1].description" :data="underMenuConstructor[1].data" @onClose="underMenuChangeState(underMenuConstructor[1].id)" />
                    </div>
                </li>
            </ul>
            <button-component :theme="[ 'b-button', 'b-button_theme_primary', 'b-header__button' ]">Join reyna chat</button-component>
            <hamburger-component @onClick="onHamburgerClick" />
        </container-component>
    </header>
</template>

<script>
import ButtonComponent from '@/components/ButtonComponent'
import UnderMenuComponent from '@/components/UnderMenuComponent'
import ContainerComponent from '@/components/ContainerComponent'
import HamburgerComponent from '@/components/HamburgerComponent'

export default {
  name: 'HeaderComponent',
  components: {
    ButtonComponent,
    UnderMenuComponent,
    ContainerComponent,
    HamburgerComponent
  },
  data () {
    return {
      windowSize: window.screen.availWidth,
      underMenuConstructor: [
        {
          id: 0,
          title: 'Projects',
          description: 'Help us #BUIDL',
          status: false,
          data: [
            {
              id: 0,
              title: 'ReynaChat',
              content: 'Chat in safest application using own encryption protocol. Test the mobile alpha.'
            },
            {
              id: 1,
              title: 'ReynaWallet',
              content: 'Wallet works with any TRC20 tokens and supports the main blockchain in the Tron Foundation.'
            },
            {
              id: 2,
              title: 'ReynaProtocol',
              content: 'Reyna protocols support mutual and optional authentication, identity hiding, forward secrecy, zero round-trip encryption, and other advanced features.'
            }
          ]
        },
        {
          id: 1,
          title: 'Contribute',
          description: 'Example label',
          status: false,
          data: [
            {
              id: 0,
              title: 'ReynaChat',
              content: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.'
            },
            {
              id: 1,
              title: 'ReynaWallet',
              content: 'Proin est elit, elementum non tellus eget, tincidunt lobortis eros.'
            },
            {
              id: 2,
              title: 'ReynaProtocol',
              content: 'Donec orci nunc, consectetur vitae convallis quis, blandit eget augue. '
            }
          ]
        }
      ],
      hamburgerStatus: false
    }
  },
  methods: {
    underMenuChangeState: function (id) {
      if (this.windowSize >= 768) {
        this.underMenuConstructor[id].status = !this.underMenuConstructor[id].status
      } else {
      }
    },
    onHamburgerClick: function () {
      this.hamburgerStatus = !this.hamburgerStatus
    }
  },
  computed: {
    navClass: function () {
      return {
        'b-header__nav': true,
        'b-header__nav_state_active': this.hamburgerStatus
      }
    },
    headerClass: function () {
      return {
        'b-header': true,
        'b-header_state_active': this.hamburgerStatus
      }
    }
  }
}
</script>

<style lang="scss">
    .b-header {
        height: 100px;

        .b-container {
            height: 100%;

            display: flex;
            align-items: center;
        }

        &__home {
            display: flex;
            align-items: center;

            outline: none;
            text-decoration: none;

            &:hover,
            &:focus {
                .b-header__logo {
                    transform: scale(0.90);
                }
            }
        }

        &__span {
            cursor: pointer;
        }

        &__logo {
            margin-right: 14.5px;
            transition: .25s ease;
        }

        &__title {
            color: #262626;
            font-size: 18px;
            font-weight: bold;
        }

        &__nav {
            padding: 0;
            margin: 0 auto 0 74px;
            list-style: none;

            display: flex;
            align-items: center;
        }

        &__item {
            &:not(:last-child) {
                margin-right: 30px;
            }
        }

        &__link {
            color: #262626;
            font-size: 16px;
            font-weight: 500;
            text-decoration: none;

            outline: none;
            transition: .15s ease;
            position: relative;

            &_hasGroup_yes {
                display: flex;
                align-items: center;
            }

            &:hover,
            &:focus {
                color: #EB5353;
            }
        }

        &__span {
            display: flex;
            align-items: center;
            &:after {
                content: "";
                position: relative;

                margin-left: 7px;
                margin-right: -7px;

                width: 10px;
                height: 5px;
                display: block;

                background-repeat: no-repeat;
                background-image: url('../assets/icon/arrow-bottom.svg');
            }
        }

        &__mobile {
            display: none;

            padding: 0 0 0 44px;
            margin: 15px -15px 0 -15px;
            list-style: none;
            line-height: 19px;

            border-top: 1px solid rgba(151, 151, 151, 0.1);
        }

        &__mobile-item {
            margin-top: 15px;

            &:not(:last-child) {
                margin-bottom: 15px;
            }
        }

        &__mobile-link {
            width: 100%;

            color: rgba(38, 38, 38, 0.5);
            font-size: 16px;
            font-weight: 500;
            text-decoration: none;
        }

        &__button {
            margin-left: auto;
        }
    }

    @media (max-width: 992px) {
        .b-header {
            &__home {
                margin: 0 auto 0 0;
            }

            &__button {
                margin: 0 0 0 auto;
            }

            &__nav {
                margin: 0 auto;
            }
        }
    }

    @media (max-width: 768px) {
        .b-header {

            &_state_active {
                z-index: 999;
                top: 0;
                position: sticky;
                transition: .15s ease;
                background-color: #fff;
            }

            &__nav {
                position: absolute;
                z-index: 999;

                top: 80px;
                left: 0;
                right: 0;

                width: 100%;

                flex-direction: column;
                background-color: #fff;

                pointer-events: none;
                opacity: 0;
                visibility: hidden;

                transition: .15s ease;

                &_state_active {
                    top: 100px;
                    pointer-events: auto;
                    opacity: 1;
                    visibility: visible;
                }
            }

            &__item {
                margin: 0;

                &:first-child {
                    .b-header__link {
                        border-top: 1px solid rgba(151, 151, 151, 0.1);
                    }
                }

                &:not(:last-child) {
                    margin: 0;
                }
            }

            &__link {
                width: 540px;
                padding: 15px 0;
                box-sizing: border-box;

                display: inline-block;

                border-bottom: 1px solid rgba(151, 151, 151, 0.1);
            }

            .b-button {
                display: none;
            }
        }
    }

    @media (max-width: 576px) {
        .b-header {

            &__item {
                width: 100%;
            }

            &__link {
                width: 100%;
                padding: 15px;
            }
        }
    }
</style>



