<template>
    <div :class="underMenuClass">
        <div class="b-under-menu__overlay">
            <button @click="onCloseUnderMenu" :class="buttonClass"></button>
            <div :class="contentClass">
                <h5 class="b-under-menu__title">{{ title }}</h5>
                <span v-show="description.length > 0" class="b-under-menu__desk">{{ description }}</span>
                <div class="b-under-menu__wrap">
                    <div v-for="item in data" v-bind:key="item.id" class="b-under-menu__item">
                        <h6 class="b-under-menu__heading">{{ item.title }}</h6>
                        <p class="b-under-menu__text">{{ item.content }}</p>
                        <learn-more-component href="#">Learn more</learn-more-component>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import LearnMoreComponent from '@/components/LearnMoreComponent'

export default {
  name: 'UnderMenuComponent',
  components: {
    LearnMoreComponent
  },
  props: {
    state: {
      type: Boolean,
      default: false,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    description: {
      type: String
    },
    data: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      objectState: this.state
    }
  },
  methods: {
    onCloseUnderMenu: function () {
      this.objectState = !this.objectState
      this.$emit('onClose')
    }
  },
  computed: {
    underMenuClass: function () {
      return {
        'b-under-menu': true,
        'b-under-menu_visible_false': this.objectState === false
      }
    },
    contentClass: function () {
      return {
        'b-under-menu__content': true,
        'b-under-menu__content_isActive_yes': this.objectState === false
      }
    },
    buttonClass: function () {
      return {
        'b-under-menu__close': true,
        'b-under-menu__close_isActive_yes': this.objectState === false
      }
    }
  },
  watch: {
    state (newState) {
      this.objectState = newState
    }
  }
}
</script>

<style lang="scss">
    .b-under-menu {
        position: fixed;

        width: 100%;
        height: 100vh;

        z-index: 999;

        top: 0;
        right: 0;
        bottom: 0;
        left: 0;

        display: flex;
        justify-content: center;

        background: rgba(0, 0, 0, 0.20);
        transition: .35s ease;
        visibility: visible;

        &_visible_false {
            opacity: 0;
            pointer-events: none;
            visibility: hidden;
        }

        &__overlay {
            width: 920px;
            height: 100%;

            position: relative;

            display: flex;
            justify-content: center;
            align-items: flex-start;
        }

        &__close {
            width: 40px;
            height: 40px;
            border-radius: 50%;

            border: none;
            outline: none;
            cursor: pointer;

            position: absolute;
            top: 100px;
            right: 0;

            background-color: rgba(255, 255, 255, 0.5);

            transform: translateY(0px);
            transition: .25s;

            &_isActive_yes {
                transform: translateY(10px);
            }

            &:after {
                position: absolute;
                content: "";

                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                margin: auto;

                width: 16px;
                height: 16px;
                display: inline-block;

                background-repeat: no-repeat;
                background-position: center;
                background-image: url('../assets/icon/close.svg');
            }

            &:hover,
            &:focus {
                background-color: #EB5353;
            }
        }

        &__content {
            box-sizing: border-box;
            margin: 169px 0 0 0;
            padding: 34px 47px 46px 39px;
            border-radius: 10px;
            background-color: #fff;

            transform: translateY(0px);
            transition: .25s;

            &_isActive_yes {
                transform: translateY(20px);
            }
        }

        &__title {
            margin: 0;
            color: #262626;
            font-size: 22px;
            line-height: 26px;
            font-weight: bold;
        }

        &__desk {
            margin: 14px 0 0 0;
            display: inline-block;
            color: rgba(38, 38, 38, 0.5);
            font-size: 14px;
            font-weight: 500;
            letter-spacing: 2px;
            line-height: 16px;
            text-transform: uppercase;
        }

        &__wrap {
            margin: 43px 0 0 0;

            display: flex;
            justify-content: space-between;
        }

        &__item {
            width: 100%;
            &:not(:last-child) {
                margin-right: 40px;
            }
        }

        &__heading {
            margin: 0;
            font-size: 20px;
            color: #262626;
            font-weight: bold;
            line-height: 24px;
        }

        &__text {
            margin: 12px 0 18px 0;
            color: rgba(38, 38, 38, 0.5);
            font-weight: 500;
            line-height: 26px;
            font-size: 16px;
        }
    }

    @media (max-width: 992px) {
        .b-under-menu {
            &__overlay {
                width: 720px;
            }
        }
    }
</style>



